$black: #000000;
$white: #ffffff;
$pink: #FF70D9;
$yellow: #EBFF00;
$darkBlue: #304571;
$darkWhite: #75819D;

@mixin button {
    cursor: pointer;
    padding: 7px 40px;
    border-radius: 100px;
    font-size: 16px;
    outline: none;
    border: none;
    font-family: "Lato", sans-serif;
    font-weight: 700;
    font-style: normal;
    transition: .3s;
    @media screen and (min-width: 768px) {
        font-size: 22px;
        padding: 14px 40px;
    }
    &:hover {
        opacity: .6;
    }
}
.header__menu-mobile {
    margin-top: -6px;
    display: block;
    @media screen and (min-width: 768px) {
        display: none;
    }

}
.header__menu-mobile-nav {
    display: none;
    .header__menu-cart {
        display: block;
        @media screen and (min-width: 768px) {
            display: none;
        }
    }
  }
  .header__menu-mobile-nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  .header__menu-mobile-nav li {
    margin-bottom: 10px;
  }
  .header__menu-mobile-nav a {
    text-decoration: none;
    color: $white;
    font-weight: bold;
  }
  #menu-toggle {
    display: none;
  }
  .menu-btn {
    display: inline-block;
    cursor: pointer;
  }
  .sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
  }
  .burger {
    display: block;
    width: 30px;
    height: 2px;
    background-color: $white;
    position: relative;
    transform-origin: 4px 0;
    transition: transform 0.5s;
  }
  .burger:before,
  .burger:after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: white;
    position: absolute;
    top: 0;;
    transition: transform 0.5s;
  }
  .burger:before {
    transform: translateY(-8px) rotate(0)
  }
  .burger:after {
    transform: translateY(8px) rotate(0);;
  }
  #menu-toggle:checked ~ .header__menu-mobile-nav {
    display: block;
  }
  #menu-toggle:checked + .menu-btn .burger {
    transform: rotate(24deg);
    background-color: black;
    left: 37px;
  }
  #menu-toggle:checked + .menu-btn .burger:before {
    transform: translateY(0) rotate(13deg);
  }
  #menu-toggle:checked + .menu-btn .burger:after {
    transform: translateY(0) rotate(-63deg);
  }

 ul.header__menu-mobile-nav-list {
    display: grid;
    color: $white;
    padding-top: 30px;
    justify-items: flex-end;
    li:hover {
        background: linear-gradient(104deg, rgba(0,0,0,0.702140231092437) 0%, rgba(235,255,0,1) 0%, rgba(255,112,217,1) 100%);
        -webkit-text-fill-color: transparent;
        -webkit-background-clip: text
    }

 }
body {
    font-family: "Lato", sans-serif;
    font-weight: 700;
    font-style: normal;
}

.header__logo svg {
    width: 71px;
    height: 24px;
    @media screen and (min-width: 1248px) {
        width: 104px;
        height: 35px;
    }
}

.header {
    display: grid;
    justify-content: space-between;
    grid-auto-flow: column;
    align-items: start;
    padding: 16px 20px 12px 20px;
    background-color: $black;
    @media screen and (min-width: 768px) {
        padding: 28px 30px;
        align-items: center;
    }
    @media screen and (min-width: 1248px) {
        padding: 24px 91px 21px 81px;
    }
}

.header__menu-desktop {
    display: grid;
    grid-auto-flow: column;
    column-gap: 55px;
    align-items: center;
}

.header__menu-list {
    display: none;
    list-style-type: none;
    grid-auto-flow: column;
    column-gap: 55px;
    color: $white;
    @media screen and (min-width: 768px) {
        display: grid;
    }
    .header__menu-list-item {
        cursor: pointer;
        transition: .3s;
    }
    .header__menu-list-item:hover {
        background: linear-gradient(104deg, rgba(0,0,0,0.702140231092437) 0%, rgba(235,255,0,1) 0%, rgba(255,112,217,1) 100%);
        -webkit-text-fill-color: transparent;
        -webkit-background-clip: text
    }
}

.header__menu-cart {
    position: relative;
    cursor: pointer;
    display: none;
    width: 25px;
    height: 17px;
    @media screen and (min-width: 768px) {
        display: block;
    }
    .header__menu-cart--default {
        opacity: 1;
    }
    .header__menu-cart--hover {
        opacity: 0;
    }
    .header__menu-cart--default, .header__menu-cart--hover {
        position: absolute;
        transition: .3s;
    }
    &:hover {
        .header__menu-cart--default {
            opacity: 0;
        }
        .header__menu-cart--hover {
            opacity: 1;
        }
    }

}

.main-screen {
    z-index: 1;
    position: relative;
    display: grid;
    justify-items: center;
    row-gap: 22px;
    padding: 249px 34px 44px 33px;
    background-image :url('./../images/EARTH.svg'), url('./../images/background_min.png');
    // background-size: cover;
    background-position: top -117px right -19px, left top;
    background-repeat: no-repeat;
    @media screen and (min-width: 400px) {
        background-position: top -117px center, left top;
    }
    @media screen and (min-width: 768px) {
        justify-items: flex-start;
        padding: 100px 30px;
        background-position: right -109px center, left top;
    }
    @media screen and (min-width: 1248px) {
        padding: 91px 81px 119px 81px;
        background-position: right 84px center, left top;
    }
    &:before {
        z-index: 0;
        content: '';
        top: 0;
        left: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(0,0,0,0.702140231092437) 0%, rgba(0,0,0,0.22034751400560226) 36%, rgba(255,255,255,0) 74%);
        @media screen and (min-width: 768px) {
            background: linear-gradient(90deg, rgba(0,0,0,0.702140231092437) 0%, rgba(0,0,0,0.22034751400560226) 32%, rgba(255,255,255,0) 74%);
        }
    }
   
}

.main-screen__header {
    z-index: 1;
    position: relative;
    color: $white;
    font-weight: 900;
    font-size: 36px;
    text-align: center;
    margin: 0;
    @media screen and (min-width: 768px) {
        font-size: 48px;
        max-width: 411px;
        text-align: left;
    }
    @media screen and (min-width: 1248px) {
        font-size: 72px;
        max-width: 670px;
    }
}
.main-screen__header-color {
    color: $pink;
    font-weight: 900;
}

.main-screen__description {
    z-index: 1;
    position: relative;
    color: $white;
    font-weight: 900;
    font-size: 16px;
    margin: 0;
    text-align: center;
    @media screen and (min-width: 768px) {
        text-align: left;
        font-size: 22px;
    }
    @media screen and (min-width: 1248px) {
        font-size: 32px;
    }
}

.main-screen__description-color {
    color: $yellow;
    font-weight: 900;
}

.main-screen__button button {
    z-index: 1;
    position: relative;
    @include button;
    background-color: $yellow;
}

.offers {
    background: linear-gradient(180deg, rgba(0,0,0,0.702140231092437) 0%, rgba(13,30,72,1) 0%, rgba(48,69,113,1) 100%);
    padding: 50px 20px;
    display: grid;
    row-gap: 30px;
    @media screen and (min-width: 1248px) {
        padding: 58px 80px;
    }
}

.offers__list {
    display: grid;
    row-gap: 30px;
    @media screen and (min-width: 768px) {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 30px;
    }
}

.offers__title {
    font-size: 18px;
    color: $white;
    font-weight: 900;
    margin: 0;
}

.offers__list-item {
    display: grid;
    row-gap: 22px;
    border-radius: 10px;
    // height: 350px;
    // width: 320px;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 100px 27px 32px 27px;
    box-sizing: border-box;
    background-position: center;
    &:first-child {
      background-image: linear-gradient(0deg, rgba(0,0,0,0.702140231092437) 0%, rgba(0,0,0,0.22034751400560226) 36%, rgba(255,255,255,0) 74%), url('./../images/Rectangle_2.png');
    }
    &:nth-child(2) {
      background-image: linear-gradient(0deg, rgba(0,0,0,0.702140231092437) 0%, rgba(0,0,0,0.22034751400560226) 36%, rgba(255,255,255,0) 74%),  url('./../images/Rectangle_3.png');
    }
    &:nth-child(3) {
      background-image: linear-gradient(0deg, rgba(0,0,0,0.702140231092437) 0%, rgba(0,0,0,0.22034751400560226) 36%, rgba(255,255,255,0) 74%), url('./../images/Rectangle_1.png');
    }
    &:nth-child(4) {
      background-image: linear-gradient(0deg, rgba(0,0,0,0.702140231092437) 0%, rgba(0,0,0,0.22034751400560226) 36%, rgba(255,255,255,0) 74%), url('./../images/Rectangle_4.png');
      
    }
    &:first-child, &:nth-child(4) {
        @media screen and (min-width: 1248px) {
            grid-column: 1/3;
            .offers__list-item-title {
                font-size: 74px;
                max-width: 780px;
            }
            .offers__list-item-description {
                font-size: 26px;
            }
          }
    }
}

.offers__list-item-title {
    font-size: 40px;
    color: $white;
    font-weight: 900;
    margin: 0;
    @media screen and (min-width: 1248px) {
        font-size: 46px;
    }
}

.offers__list-item-description {
    font-size: 16px;
    color: $white;
    font-weight: 900;
    margin: 0;
    @media screen and (min-width: 1248px) {
        font-size: 24px;
    }
}

.offers__list-item-button button {
    @include button();
    background-color: transparent;
    border: 2px solid $yellow;
    color: $yellow;
}

.offers__footer-title {
    font-size: 18px;
    color: $white;
    font-weight: 900;
    margin: 0;
    @media screen and (min-width: 768px) {
        font-size: 24px;
    }
    @media screen and (min-width: 1248px) {
        font-size: 32px;
    }
}

.offers__footer-description {
    font-size: 14px;
    color: $darkWhite;
    @media screen and (min-width: 768px) {
        font-size: 16px;
    }
    @media screen and (min-width: 1248px) {
        font-size: 20px;
    }
}

.offers__footer-link {
    cursor: pointer;
    color: $yellow;
    font-size: 14px;
    font-weight: 900;
    text-decoration: none;
    transition: .3s;
    @media screen and (min-width: 768px) {
        font-size: 16px;
    }
    @media screen and (min-width: 1248px) {
        font-size: 20px;
    }
    &:hover {
        opacity: .6;
    }
}

.footer {
    display: grid;
    justify-items: center;
    background-color: $black;
    padding-bottom: 20px;
}

.footer__text {
    color: $yellow;
    font-size: 18px;
    font-weight: 900;
    margin: 0;
}

.footer__image {
    width: 130px;
    height: 120px;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url('./../images/footer_logo.png');
}

.offers__footer-description--hidden {
    display: none;
}

.offers__footer-view-more:not(:checked) ~ label,
.offers__footer-view-more:checked ~ .offers__footer-description--hidden {
  display: block;
}

.offers__footer-view-more,
.offers__footer-view-more:checked ~ label {
  display: none;
}